"use client"

import { useState, useEffect, useRef } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Link from "next/link"

// Definição simplificada de permissões para teste
const permissoesTeste = {
  ESTOQUE_VISUALIZAR: true,
  ESTOQUE_ADICIONAR: false,
  VENDAS_VISUALIZAR: true,
  VENDAS_CRIAR: false,
  FILA_VISUALIZAR: true,
  USUARIOS_VISUALIZAR: false,
}

export default function PermissoesPersistentePage() {
  // Usar localStorage para persistir as permissões entre recarregamentos
  const [permissoes, setPermissoes] = useState(() => {
    // Tentar carregar do localStorage apenas no cliente
    if (typeof window !== "undefined") {
      const savedPermissoes = localStorage.getItem("permissoesTeste")
      return savedPermissoes ? JSON.parse(savedPermissoes) : permissoesTeste
    }
    return permissoesTeste
  })

  const [logs, setLogs] = useState<string[]>([])
  const mountedRef = useRef(false)
  const checkboxesRef = useRef<HTMLDivElement>(null)

  // Função para adicionar logs
  const addLog = (message: string) => {
    const timestamp = new Date().toISOString().split("T")[1].split(".")[0]
    setLogs((prev) => [...prev, `${timestamp}: ${message}`])
  }

  // Efeito para registrar quando o componente é montado
  useEffect(() => {
    addLog("Componente montado com sucesso")
    addLog(`Estado inicial das permissões: ${JSON.stringify(permissoes)}`)

    // Verificar se os checkboxes estão visíveis
    setTimeout(() => {
      if (checkboxesRef.current) {
        const checkboxes = checkboxesRef.current.querySelectorAll('input[type="checkbox"]')
        addLog(`Número de checkboxes encontrados: ${checkboxes.length}`)

        checkboxes.forEach((checkbox, index) => {
          // Fazer cast para HTMLElement para acessar offsetWidth e offsetHeight
          const htmlCheckbox = checkbox as HTMLElement
          const isVisible = !!(
            htmlCheckbox.offsetWidth ||
            htmlCheckbox.offsetHeight ||
            htmlCheckbox.getClientRects().length
          )
          addLog(`Checkbox ${index} visível: ${isVisible}`)
        })
      }
    }, 500)

    mountedRef.current = true

    return () => {
      addLog("Componente desmontado")
      mountedRef.current = false
    }
  }, [])

  // Efeito para salvar as permissões no localStorage quando elas mudarem
  useEffect(() => {
    if (mountedRef.current && typeof window !== "undefined") {
      localStorage.setItem("permissoesTeste", JSON.stringify(permissoes))
      addLog("Permissões salvas no localStorage")
    }
  }, [permissoes])

  // Função para alternar permissão
  const togglePermissao = (permissaoId: string) => {
    addLog(`Alternando permissão: ${permissaoId}`)
    setPermissoes((prev) => {
      const novoEstado = {
        ...prev,
        [permissaoId]: !prev[permissaoId as keyof typeof prev],
      }
      return novoEstado
    })
  }

  return (
    <div className="container mx-auto py-6">
      <h1 className="text-2xl font-bold mb-6">Permissões Persistentes (Teste)</h1>

      <div className="mb-4">
        <Link href="/usuarios/permissoes/simplificado" className="text-blue-600 hover:underline">
          Voltar para Versão Simplificada
        </Link>
      </div>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Permissões de Teste (Persistentes)</CardTitle>
        </CardHeader>
        <CardContent>
          <div ref={checkboxesRef} className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {Object.entries(permissoes).map(([permissaoId, valor]) => (
              <div key={permissaoId} className="flex items-center space-x-2">
                {/* Checkbox com estilos inline explícitos */}
                <input
                  type="checkbox"
                  id={permissaoId}
                  checked={!!valor}
                  onChange={() => togglePermissao(permissaoId)}
                  style={{
                    width: "20px",
                    height: "20px",
                    opacity: "1",
                    position: "static",
                    appearance: "auto",
                    visibility: "visible",
                    display: "inline-block",
                    margin: "0",
                    padding: "0",
                    zIndex: "auto",
                  }}
                />
                <label
                  htmlFor={permissaoId}
                  className="text-sm font-medium cursor-pointer"
                  onClick={() => togglePermissao(permissaoId)}
                >
                  {permissaoId}
                </label>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Logs</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="bg-gray-100 p-4 rounded-md">
            <pre className="whitespace-pre-wrap text-sm">
              {logs.map((log, index) => (
                <div key={index}>{log}</div>
              ))}
            </pre>
          </div>
        </CardContent>
      </Card>

      <div className="flex justify-end">
        <Button
          onClick={() => {
            addLog("Botão de teste clicado")
            alert("Estado atual das permissões: " + JSON.stringify(permissoes, null, 2))
          }}
        >
          Testar Estado
        </Button>
      </div>
    </div>
  )
}
